@import "./variables.scss";

/* ---------- typography ----------  */

//lo nombre como 'title' porque cunia no tiene mas modificaciones sino solo el tamao :D
@mixin title {
  font-family: $font-1;
  font-size: $font__size-h1;
}

//aqui pasa igual solo modifica el tamao en 3 ocasiones y todas son bold
@mixin subtitle {
  font-family: $font-2;
  font-size: $font__size-h4;
  font-weight: bold;
}

@mixin p {
  font-family: $font-2;
  font-size: $font__size-p;
  font-weight: 400;
}

@mixin a {
  font-family: $font-1;
  font-size: $font__size-a;
}

@mixin strong {
  font-family: $font-2;
  font-size: $font__size-strong;
  font-weight: bold;
}

/* ---------- icon ----------  */

@mixin icon {
  width: 80px;
  height: 79px;
}

/* ---------- Buttons ----------  */

@mixin button-primary-medium {
  width: 320px;
  height: 60px;
  border-radius: $border-radius;
  background: $primary-gradient;
}
@mixin button-secondary-medium {
  width: 240px;
  height: 50px;
  border-radius: $border-radius;
  background: $secondary-gradient;
}
@mixin button-purple-medium {
  width: 240px;
  height: 50px;
  border-radius: $border-radius;
  background: $primary-purple;
}

/* ---------- small-Buttons ----------  */

@mixin button-primary-small {
  width: 180px;
  height: 50px;
  border-radius: $border-radius-small;
  background: $primary-gradient;
}
@mixin button-secondary-small {
  width: 180px;
  height: 50px;
  border-radius: $border-radius-small;
  background: $secondary-gradient;
}
@mixin button-purple-small {
  width: 160px;
  height: 40px;
  border-radius: $border-radius-small;
}
